<script>
import ProductScrollSection from '../components/ProductScrollSection.vue'
import loader from '../mixins/loader'
// import ProductBox from '../components/Boxes/ProductBox'
// import BrandBox from '../components/Boxes/BrandBox'
// import CategoryBox from '../components/Boxes/CategoryBox'
export default {
  data () {
    return {
      flashSaleProducts: null,
      topRatedProducts: null,
      mostSoldProducts: null,
      newArrivalsProducts: null,
      ourShopProducts: null,
      topBrands: null,
      sliding: false,
      images: [],
      categories: []
    }
  },
  components: {
    ProductScrollSection
    // ProductBox,
    // BrandBox
    // CategoryBox
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadFlashSale () {
      const offset = 0
      const limit = 5
      this.loader('/products/flash-sale', { offset, limit }, 'flash-sale-home')
        .then(response => {
          this.flashSaleProducts = response.data.products
        })
    },
    loadTopRated () {
      const offset = 0
      const limit = 5
      this.loader('/products/top-rated', { offset, limit }, 'top-rated-home')
        .then(response => {
          this.topRatedProducts = response.data.products
        })
    },
    loadMostSold () {
      const offset = 0
      const limit = 5
      this.loader('/products/most-sold', { offset, limit }, 'most-sold-home')
        .then(response => {
          this.mostSoldProducts = response.data.products
        })
    },
    loadTopBrands () {
      const offset = 0
      const limit = 5
      this.loader('/top-brands', { offset, limit }, 'top-brands-home')
        .then(response => {
          this.topBrands = response.data.brands.map(brand => {
            brand.shadow = false
            return brand
          })
          // console.log(response.data)
        })
    },
    loadNewArrivals () {
      const offset = 0
      const limit = 4
      this.loader('/products/new-arrivals', { offset, limit }, 'new-arrivals-home')
        .then(response => {
          this.newArrivalsProducts = response.data.products
        })
    },
    loadOurShop () {
      const offset = 0
      const limit = 5
      this.loader('/products/our-shop', { offset, limit }, 'our-shop-home')
        .then(response => {
          this.ourShopProducts = response.data.products
        })
    },
    loadSlideImages () {
      this.loader('/banner/all', { type: 'homePage' }, 'banner-home')
        .then(response => {
          this.images = response.data
        })
    },
    loadCategories () {
      const offset = 0
      const limit = 4
      this.loader('/categories/all', { offset, limit }, 'categories-home')
        .then(response => {
          this.categories = response.data.categories.map(category => {
            category.shadow = false
            return category
          })
        })
    },
    loadAll () {
      this.loadFlashSale()
      this.loadTopRated()
      this.loadNewArrivals()
      this.loadMostSold()
      this.loadTopBrands()
      this.loadOurShop()
      this.loadCategories()
      this.loadSlideImages()
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadAll()
    }
  },
  created () {
    this.loadAll()
  }
}
</script>

